#mine{
    overflow: hidden;
    .head{
        position: relative;
        height: 44px;
        background: rgba($color: #000000, $alpha: 0.5);
        text-align: center;
        line-height: 44px;
        .iconfont{
            position: absolute;
            font-size: 30px;
            color: #fff;
            top: 0;
        }
        .iconfont:nth-of-type(1){
            left: 10px;
        }
        .iconfont:nth-of-type(2){
            right: 10px;
        }
    }
    #setting{
       h3 {
        text-align:center;
        height:44px;
        line-height:44px;
        position:relative;
        margin-bottom:10px;
        font-size:20px;
        box-shadow: 3px 3px 3px rgba($color: #000000, $alpha:0.2);
       }
    }
}